@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;500;600;700;800&display=swap');

:root {
    --accent: #61dbfb;
    --light: #fff;
    --dark: #242424;
    --grey: #6a6a6a;
}

* {
    box-sizing: border-box;
}

body {
    margin: 0;
    font-family: 'Poppins', Arial, Helvetica, sans-serif;
}

nav {
    background-color: var(--light);  
    height: 4.5rem;
    display: flex;
    align-items: center;
    padding: 1rem 2rem;
    box-shadow: 0 2px 4px #a5a1a1;
    z-index: 10;
    position: relative;
}

nav img {
    height: 100%;
    margin-right: 1rem;
}

nav h3 {
    margin-right: auto;
    color: var(--accent);
    font-size: 1.4rem;
}

.toggler {
    color: var(--dark);
    font-size: 0.8rem;
    font-weight: 600;
    display: flex;
    align-items: center;
    gap: 0.4rem;
}

.toggler input {
    display: none;
}

.toggler label {
    display: flex;
    height: 1.4rem;
    width: 2.5rem;
    border-radius: 2rem;
    background-color: var(--dark);
    position: relative;
}

.toggler label::before {
    content: '';
    height: 95%;
    aspect-ratio: 1;
    background-color: var(--light);
    position: absolute;
    border-radius: 50%;
}

.darkLabel {
    color: var(--grey);
}

.toggler input:checked ~ label::before {
    right: 0;
}
.toggler input:checked ~ .lightLabel {
    color: var(--grey);
}
.toggler input:checked ~ .darkLabel {
    color: var(--dark);
}

main {
    padding: 1rem 2rem;
    height: calc(100vh - 4.5rem);
}

main h1 {
    font-size: 3rem;
    line-height: 1.2;
    font-weight: 800;
    color: var(--dark);
}

main ul {
    font-size: 1.3rem;
    list-style-type: none;
}

main ul li {
    position: relative;
    margin-bottom: 1rem;
}

main ul li::before {
    content: '';
    position: absolute;
    top: 0.7em;
    left: -2rem;
    transform: translateY(-50%);
    height: 0.8em;
    aspect-ratio: 1;
    background-color: var(--accent);
    border-radius: 1em;
}

nav.dark,
nav.dark .toggler label::before,
main.dark {
    background-color: var(--dark);
}

nav.dark .toggler label {
    background-color: var(--light);
}

nav.dark .toggler,
nav.dark .toggler input:checked ~ .darkLabel,
main.dark h1,
main.dark li {
    color: var(--light);
}